  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  height: 100%;
  min-height: 100%;
  font-size:14px;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
  background-color: #eeeeee;
  font-family: 'Montserrat', sans-serif;
  min-height: 100%;
}

.App__body{
  width:1050px;
  max-width:100%;
  margin: auto;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  grid-column-gap: 10px;
  -webkit-column-gap: 10px;
          column-gap: 10px;
  height: 100%;
  padding-top: 20px;
}

.App__profile{
  width:250px;
}
.App__feed{
  width:500px;
}
.ads{
  width: 250px;
  background-color: #ffffff;

}

/*Header*/
.header{
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  padding: 10px 20px;
  background-color: #A95C68;
  color: #ffffff;
}

Button{
  color: #fff;
}

.header__button{
  color:#A95C68;
  background-color: #ffffff;
}
.header__button:hover{
  color:#ffffff;
}

.Header__sigin{
  background: none;
  outline:none;
  border:none;
  color:#005b96;
  cursor: pointer;
}
.Header__sigin:hover{
  text-decoration: underline;
}

@media only screen and (max-width: 1024px){
  .ads{
    display: none;
  }
}

